<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="游戏名称" prop="youximingcheng" >
					<el-input v-model="ruleForm.youximingcheng" placeholder="游戏名称" clearable  :readonly="ro.youximingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="游戏名称" prop="youximingcheng" >
					<el-input v-model="ruleForm.youximingcheng" placeholder="游戏名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="游戏分类" prop="youxifenlei" >
					<el-input v-model="ruleForm.youxifenlei" placeholder="游戏分类" clearable  :readonly="ro.youxifenlei"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="游戏分类" prop="youxifenlei" >
					<el-input v-model="ruleForm.youxifenlei" placeholder="游戏分类" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.tupian" label="图片" prop="tupian" >
					<file-upload
						tip="点击上传图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.tupian?ruleForm.tupian:''"
						@change="tupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.tupian" label="图片" prop="tupian" >
					<img v-if="ruleForm.tupian.substring(0,4)=='http'&&ruleForm.tupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian" width="100" height="100">
					<img v-else-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="链接" prop="lianjie" >
					<el-input v-model="ruleForm.lianjie" placeholder="链接" clearable  :readonly="ro.lianjie"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="链接" prop="lianjie" >
					<el-input v-model="ruleForm.lianjie" placeholder="链接" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info'&& !ro.youxishipin" label="游戏视频" prop="youxishipin" >
					<file-upload
						tip="点击上传游戏视频"
						action="file/upload"
						:limit="1"
						:type="2"
						:multiple="true"
						:fileUrls="ruleForm.youxishipin?ruleForm.youxishipin:''"
						@change="youxishipinUploadChange"
					></file-upload>
				</el-form-item> 
				<el-form-item v-else-if="ruleForm.youxishipin" label="游戏视频" prop="youxishipin" >
					<el-button class="viewBtn" type="text" size="small" @click="download($base.url+ruleForm.youxishipin)">
						<span class="icon iconfont icon-chakan14"></span>
						预览
					</el-button>
				</el-form-item>
				<el-form-item v-else-if="!ruleForm.youxishipin" label="游戏视频" prop="youxishipin" >
					<el-button class="unBtn" type="text" size="small">
						<span class="icon iconfont icon-xihuan"></span>
						暂无
					</el-button>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="发售日期" prop="fashouriqi" >
					<el-date-picker
						format="yyyy 年 MM 月 dd 日"
						value-format="yyyy-MM-dd"
						v-model="ruleForm.fashouriqi" 
						type="date"
						:readonly="ro.fashouriqi"
						placeholder="发售日期"
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.fashouriqi" label="发售日期" prop="fashouriqi" >
					<el-input v-model="ruleForm.fashouriqi" placeholder="发售日期" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="制作" prop="zhizuo" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder="制作"
					v-model="ruleForm.zhizuo" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.zhizuo" label="制作" prop="zhizuo" >
				<span class="text">{{ruleForm.zhizuo}}</span>
			</el-form-item>
			<el-form-item v-if="type!='info'"  label="游戏详情" prop="youxixiangqing" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.youxixiangqing" 
					class="editor"
					myQuillEditor="youxixiangqing"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.youxixiangqing" label="游戏详情" prop="youxixiangqing" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.youxixiangqing"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
		isURL,
	} from "@/utils/validate";
	export default {
		data() {
			var validateUrl = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isURL(value)) {
					callback(new Error("请输入正确的URL地址"));
				} else {
					callback();
				}
			};
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error("请输入整数"));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					youximingcheng : false,
					youxifenlei : false,
					tupian : false,
					zhizuo : false,
					lianjie : false,
					youxishipin : false,
					fashouriqi : false,
					youxixiangqing : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
			
				ruleForm: {
					youximingcheng: '',
					youxifenlei: '',
					tupian: '',
					zhizuo: '',
					lianjie: '',
					youxishipin: '',
					fashouriqi: '',
					youxixiangqing: '',
					clicktime: '',
				},

				rules: {
					youximingcheng: [
						{ required: true, message: '游戏名称不能为空', trigger: 'blur' },
					],
					youxifenlei: [
					],
					tupian: [
					],
					zhizuo: [
					],
					lianjie: [
						{ validator: validateUrl, trigger: 'blur' },
					],
					youxishipin: [
					],
					fashouriqi: [
					],
					youxixiangqing: [
					],
					thumbsupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.fashouriqi = this.getCurDate()
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='youximingcheng'){
							this.ruleForm.youximingcheng = obj[o];
							this.ro.youximingcheng = true;
							continue;
						}
						if(o=='youxifenlei'){
							this.ruleForm.youxifenlei = obj[o];
							this.ro.youxifenlei = true;
							continue;
						}
						if(o=='tupian'){
							this.ruleForm.tupian = obj[o];
							this.ro.tupian = true;
							continue;
						}
						if(o=='zhizuo'){
							this.ruleForm.zhizuo = obj[o];
							this.ro.zhizuo = true;
							continue;
						}
						if(o=='lianjie'){
							this.ruleForm.lianjie = obj[o];
							this.ro.lianjie = true;
							continue;
						}
						if(o=='youxishipin'){
							this.ruleForm.youxishipin = obj[o];
							this.ro.youxishipin = true;
							continue;
						}
						if(o=='fashouriqi'){
							this.ruleForm.fashouriqi = obj[o];
							this.ro.fashouriqi = true;
							continue;
						}
						if(o=='youxixiangqing'){
							this.ruleForm.youxixiangqing = obj[o];
							this.ro.youxixiangqing = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `youxixinxi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
						this.ruleForm.youxixiangqing = this.ruleForm.youxixiangqing.replace(reg,'../../../springboot8w0i844u/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					if(this.ruleForm.tupian!=null) {
						this.ruleForm.tupian = this.ruleForm.tupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					if(this.ruleForm.youxishipin!=null) {
						this.ruleForm.youxishipin = this.ruleForm.youxishipin.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `youxixinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.youxixinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.youxixinxiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			tupianUploadChange(fileUrls) {
				this.ruleForm.tupian = fileUrls;
			},
			youxishipinUploadChange(fileUrls) {
				this.ruleForm.youxishipin = fileUrls;
			},
		}
	};
</script>
